@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
}

.App {
  background-color: #f9f9f9;
  color: #212121;
  font-family: "Roboto", sans-serif;
}

#home {
  height: 100%; /* 100vh forces to cover the screen. 100% only cover the screen if there is a lot of content. */
}
#home .container {
  margin: 0 auto;
  padding-top: 32px;
  max-width: 320px;
}

#home .container > .title {
  padding-left: 8px;
  margin-bottom: 16px;
  font-size: 20px;
  letter-spacing: -0.75px;
}

/* YouTube breakpoints */
@media only screen and (min-width: 500px) {
  #home .container {
    max-width: calc(
      2 * 320px + 16px
    ); /* the max width is 2 columns + the gap */
  }
  #home .grid {
    display: grid; /* the native way of creating columns withouth hacks or frameworks */
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 16px;
  }
}

@media only screen and (min-width: 660px) {
  #home .container {
    padding-left: 20px;
    padding-right: 20px;
  }

  #home .container .title {
    padding-left: 0;
  }
}

@media only screen and (min-width: 900px) {
  #home .container {
    max-width: calc(3 * 320px + 16px);
  }

  #home .grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media only screen and (min-width: 1000px) {
  #home .container {
    max-width: 960px;
  }

  #home .grid {
    grid-template-columns: repeat(4, 1fr);
  }
}

#results {
  height: 100%; /* 100vh forces to cover the screen. 100% only cover the screen if there is a lot of content. */
}

#results .container {
  margin: 0 auto;
  padding-top: 32px;
  max-width: 320px;
}

#results .container > .title {
  padding-left: 8px;
  margin-bottom: 16px;
  font-size: 20px;
  letter-spacing: -0.75px;
}

/* YouTube breakpoints */
@media only screen and (min-width: 500px) {
  #results .container {
    max-width: 100%;
    margin-left: 16px;
  }

  #results .card {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
  }

  #results .card .preview {
    width: auto;
    height: 160px;
    margin-right: 8px;
  }

  #results .card .channel-thumb {
    display: none;
  }
}

.VideoPage {
  padding-bottom: 32px;
}

.VideoPage .video-container {
  background-color: black;
  margin-bottom: 24px;
}

.VideoPage .body-container {
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.VideoPage video {
  display: block; /* To remove a small space on the bottom */
  margin: 0 auto;
  width: 100%;
  height: auto;
}

.VideoPage .channel-thumb {
  width: 48px !important; /* overrides meta-data size */
}

.VideoPage .title {
  font-size: 21px;
}

.VideoPage .description {
  font-size: 14px;
  max-width: 640px;
}

/* YouTube breakpoints */
@media only screen and (min-width: 900px) {
  .VideoPage video {
    width: 850px;
  }
  .VideoPage .body-container {
    padding: 0;
    max-width: 960px;
  }
}

.card {
  box-sizing: border-box;
  margin: 0 auto 24px auto;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: default;
}

.card .preview {
  width: 100%;
  height: auto;
  margin-bottom: 8px;
}

@media only screen and (min-width: 900px) {
  .card .meta-data {
    padding: 0;
  }
}

.header {
  padding: 12px 16px;

  display: grid;
  grid-template-columns: 1fr 1fr; /* each fr is a equal space column. */
  grid-column-gap: 16px;
}

.header.light {
  background-color: white;
}

.header.dark {
  background-color: #252525;
}

.header img {
  max-width: 120px;
  height: auto;
  vertical-align: -webkit-baseline-middle;
}

.header.dark .search-bar input {
  background-color: #121212;
  color: #f8f8f8;
}

.header.dark .search-bar button {
  background-color: #363636;
  color: #727272;
}

@media only screen and (min-width: 660px) {
  .header {
    grid-template-columns: 1fr 1fr 1fr; /* each fr is a equal space column. */
  }
}

.meta-data {
  display: -webkit-flex;
  display: flex;
  padding: 0 8px;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.meta-data .left {
  margin-right: 8px;
}

.meta-data .channel-thumb {
  border-radius: 500px; /* A high radius to make it round */
  width: 36px;
  height: auto;
}

.meta-data .title,
.meta-data .description {
  font-size: 14px;
}

.meta-data .title {
  margin-bottom: 8px;
}

.meta-data .description {
  margin-bottom: 4px;
}

.meta-data .description {
  color: grey;
}

/* The search field */
.search-bar input[type="text"] {
  box-sizing: border-box;
  display: inline-block;
  width: calc(100% - 64px - 10px); /* 48px is the width of the search button */
  height: 36px;
  padding: 8px;
  font-size: 14px;
  border: solid 0.5px #828282;
  outline: none;
}

/* The submit button */
.search-bar input[type="submit"] {
  box-sizing: border-box;
  display: inline-block;
  width: 64px;
  height: 36px;
  background-color: #f8f8f8;
  color: #828282;
  border: solid 0.5px #828282;
  text-align: center;
  vertical-align: top;
  text-decoration: none;
  line-height: 36px;
}

.button {
  padding: 8px 8px;
  font-weight: bold;
  background-color: crimson;
  color: white;
  font-size: 12px;
  text-transform: uppercase;
  text-decoration: none;
}

.button:hover {
  background-color: black;
}

hr {
  border-top: solid 0.5px lightgray;
}

h1 {
  letter-spacing: -0.5px; /* to make look closer to the original YouTube font */
}

h1,
p {
  margin-top: 0;
}

